<template>
  <div class="container mt-3">
    <b-breadcrumb>
      <b-breadcrumb-item @click="chwd('/')">
        <b-icon icon="house-fill" scale="1.25" shift-v="1.25" aria-hidden="true"></b-icon>Home
      </b-breadcrumb-item>
      <b-breadcrumb-item
        v-for="e in navPathArr"
        @click.prevent="chwd(e.href)"
        :href="e.href"
        :key="e.href"
      >{{ e.path }}</b-breadcrumb-item>
    </b-breadcrumb>
    <div class="mt-3">
      <aplayer
        v-if="audioList.length>0"
        :list="audioList"
        :music.sync="aplayerData.music"
        :repeat.sync="aplayerData.repeat"
        :shuffle.sync="aplayerData.shuffle"
      />
    </div>
    <b-table
      hover
      responsive
      :items="list"
      :fields="fields"
      primary-key="nameFormat"
      head-variant="light"
      class="border rounded mt-2"
      :per-page="pageData.perPage"
      :current-page="pageData.currentPage"
    >
      <template v-slot:cell(nameFormat)="data">
        <template v-if="data.item.type===1">
          <b-icon icon="folder" scale="1.25" shift-h="-3" aria-hidden="true"></b-icon>
          <a
            :href="data.item.name + '/'"
            @click.prevent="chwd(data.item.name + '/')"
          >{{ data.item.name }}/</a>
        </template>
        <template v-else-if="data.item.type===3">
          <b-icon icon="inbox" scale="1.25" shift-h="-3" aria-hidden="true"></b-icon>
          <a
            :href="data.item.name + '/'"
            @click.prevent="chwd(data.item.name + '/')"
          >{{ data.item.name }}/</a>
        </template>
        <template v-else>
          <b-icon icon="document" scale="1.25" shift-h="-3" aria-hidden="true"></b-icon>
          <a
            :href="data.item.name+'?preview'"
            @click.prevent="preview(data.item)"
            class="mr-2"
          >{{ data.item.name }}</a>
          <a :href="common.p_h0+nowPath+data.item.name" target="_blank">
            <b-icon icon="download" scale="1.25" shift-h="-3" aria-hidden="true"></b-icon>
          </a>
        </template>
      </template>

      <template v-slot:table-busy>
        <div class="text-center text-primary my-2">
          <b-spinner class="align-middle"></b-spinner>
          <strong>{{ loadingData.info }}</strong>
        </div>
      </template>
    </b-table>
    <div class="overflow-auto" style="display: flex;justify-content: flex-end;">
      <b-pagination
        v-model="pageData.currentPage"
        :total-rows="list.length"
        :per-page="pageData.perPage"
      ></b-pagination>
      <b-form-select
        v-model="pageData.perPage"
        :options="pageData.pageOptions"
        class="mb-3 ml-2"
        style="width: 100px;"
      ></b-form-select>
      <b-button
        v-if="next"
        @click="updateList(nowPath,next)"
        class="ml-2 mb-3"
      >{{loadingData.isBusy?'loading...':'LoadMore'}}</b-button>
    </div>

    <b-modal v-model="dplayerData.isBusy" centered hide-footer :title="'title'" size="xl">
      <video-player ref="videoPlayer" :url="dplayerData.url" :path="dplayerData.path" />
    </b-modal>
  </div>
</template>
<script>
import VideoPlayer from "../components/preview/VideoPlayer";
import Aplayer from "vue-aplayer";
export default {
  components: {
    VideoPlayer,
    aplayer: Aplayer
  },
  data() {
    return {
      aplayerData: {
        music: {
          src: "https://google.com/404"
        },
        repeat: "no-repeat",
        shuffle: false
      },
      dplayerData: {
        isBusy: false,
        url: "",
        path: ""
      },

      nowPath: "/",
      listCache: {},
      pageData: {
        currentPage: 1,
        perPage: 10,
        pageOptions: [
          {
            text: "10/Page",
            value: 10
          },
          {
            text: "50/Page",
            value: 50
          }
        ]
      },
      loadingData: {
        isBusy: false,
        info: "default info"
      },
      fields: [
        {
          key: "nameFormat",
          label: "Name"
        },
        {
          key: "time",
          label: "Time",
          thStyle: "width:180px",
          formatter: this.common.formatDate
        },
        {
          key: "size",
          label: "Size",
          thStyle: "width:120px",
          class: "text-right mr-3",
          formatter: this.common.formatSize
        }
      ]
    };
  },
  created: function() {
    this.nowPath = this.$route.params.pathMatch || "/";
    this.updateList(this.nowPath);
  },
  methods: {
    chwd(path) {
      if (path.startsWith("/")) this.updateList(path);
      else this.updateList(this.nowPath + path);
    },
    updateList(path, spPage = "") {
      //spPaeg为?page=xxxx或者spPage=xxx
      if (this.nowPath !== path) {
        this.$router.push({ path: "/home" + path });
        this.nowPath = path;
        if (this.listCache[path]) return;
      }
      if (this.loadingData.isBusy)
        return this.$message("Processing other tasks, try again later", "i");
      this.loadingData.isBusy = true;
      this.loadingData.info = "loading... " + path + spPage;
      console.log("try: ls " + path + spPage);
      let _this = this;
      let url =
        `.${this.common.urlSpCharEncode(path)}?json`  +
        (spPage ? "&" + spPage.slice(1) : "");
      this.common.axios
        .get(url)
        .then(r => {
          _this.loadingData.isBusy = false;
          if (r.data.list) {
            if (!spPage) _this.$set(_this.listCache, path, r.data);
            else {
              r.data.list.forEach(e => _this.listCache[path].list.push(e));
              _this.listCache[path].next = r.data.next;
            }
          } else console.log(r.data);
        })
        .catch(e => {
          _this.loadingData.isBusy = false;
          console.log(e);
          let msg = e.response
            ? "error: " + e.response.data.info
            : "error: " + e.message;
          _this.$message(msg, "d");
          if (e.response.status === 401) {
            let p = e.response.data.info.split(":");
            _this.$prompt({
              message: p[1],
              ok: v => {
                let d = {};
                d[p[0]] = v;
                _this.common.axios
                  .post(url, d)
                  .then(() => {
                    _this.$message("验证正确", "i");
                    _this.$router.push({ path: "/home" + path });
                  })
                  .catch(e => {
                    console.log(e);
                    let msg = e.response
                      ? "error: " + e.response.data.info
                      : "error: " + e.message;
                    _this.$message(msg, "d");
                  });
              }
            });
          }
        });
    },
    preview(file) {
      if (file.mime.startsWith("audio"))
        return this.$message("音乐播放面板它不香么?", "i");
      else if (file.mime.startsWith("video")) {
        this.dplayerData.url = null;
        this.dplayerData.path = this.nowPath + file.name;
        this.dplayerData.isBusy = true;
      } else {
        window.open(
          this.common.p_h0 + this.nowPath + file.name + "?preview",
          "_blank"
        );
      }
    }
  },
  computed: {
    navPathArr: function() {
      let path = this.nowPath;
      let tmpPath = "/";
      let arr = [];
      path.split("/").forEach(e => {
        if (!e) return;
        tmpPath += e;
        tmpPath += "/";
        arr.push({
          path: e,
          href: tmpPath
        });
      });
      return arr;
    },
    list: function() {
      let c = this.listCache[this.nowPath] || {
        list: [
          {
            type: 3,
            name: this.loadingData.isBusy ? "loading..." : "loading failed",
            size: NaN,
            time: undefined,
            mime: ""
          }
        ]
      };
      return c.list;
    },
    next: function() {
      let n = this.listCache[this.nowPath];
      if (n) n = n.next;
      return n;
    },
    audioList: function() {
      let l = [];
      this.list.forEach(e => {
        if (e.mime.startsWith("audio")) {
          l.push({
            title: e.name,
            src: this.common.p_h0 + this.nowPath + e.name
          });
        }
      });
      return l;
    }
  }
};
</script>