<template>
  <b-modal
    v-model="isShow"
    :centered="true"
    :title="title"
    :cancel-title="cancelTitle"
    :ok-title="okTitle"
    @ok="modalOk"
    @cancel="modalCancel"
    @close="modalClose"
  >
    <p class="my-4">{{message}}</p>
  </b-modal>
</template>
<script>
export default {
  props: {
    isShow: {
      type: Boolean,
      default: false
    },
    message: {
      type: [String, Number],
      default: "default message"
    },
    title: {
      type: [String],
      default: "Confirm"
    },
    cancelTitle: {
      type: String,
      default: "取消"
    },
    okTitle: {
      type: String,
      default: "确定"
    },
    ok: {
      type: Function,
      default: null
    },
    cancel: {
      type: Function,
      default: null
    },
    close: {
      type: Function,
      default: null
    }
  },
  methods: {
    modalOk() {
      if (this.ok) this.ok();
    },
    modalCancel() {
      if (this.cancel) this.cancel();
    },
    modalClose() {
      if (this.close) this.close();
    }
  }
};
</script>