<template>
  <div class="container">
    <div class="row justify-content-center mt-3">
      <b-card header="Login" class="col-md-5 p-0 mt-3">
        <b-form-group label="UserName">
          <b-form-input v-model="loginForm.username" trim></b-form-input>
        </b-form-group>
        <b-form-group label="Password">
          <b-form-input v-model="loginForm.password" trim></b-form-input>
        </b-form-group>
        <b-button variant="primary" @click="submit">登录</b-button>
      </b-card>
    </div>
  </div>
</template>
<script>
export default {
  computed: {},
  data() {
    return {
      loginForm: {
        username: "",
        password: ""
      }
    };
  },
  methods: {
    submit: function() {
      let _this = this;
      this.common.axios.post("./admin/ajax/login", this.loginForm).then(()=>{
        _this.$message("登录成功","i");
        _this.$store.commit('loginSuccess')
        _this.$router.push({ path: "/dashboard" });
      }).catch((e)=>{
        if(e.response)_this.$message(e.response.data.info,"d");
        else _this.$message(e.message,"d");
      });
    }
  }
};
</script>