import Prompt from './main.vue'
const P = {};
const defaultP = {
    isShow: true,
    message: "default message",
    title: "input",
    cancelTitle: "取消",
    okTitle: "确定",
    ok: null,
    cancel: null,
    close: null,
    regex: /.+/,
    invalidTips: ''
};
P.install = function (Vue) {
    const PromptConstructor = Vue.extend(Prompt);
    const instance = new PromptConstructor();
    instance.$mount()
    document.body.appendChild(instance.$el)
    Vue.prototype.$prompt = (options) => {
        Object.assign(instance, defaultP, options);
    }
}
export default P