<template>
  <div id="dplayer"></div>
</template>

<script>
import "dplayer/dist/DPlayer.min.css";

let DPlayer;
// import flvjs from "flv.js";
// import Hls from "hls.js";
require.ensure(
  [],
  function() {
    DPlayer = require("dplayer");
  },
  "dplayer"
);

// window.flvjs = flvjs;
// window.Hls = Hls;

export default {
  name: "VideoPlayer",
  data() {
    return {
      options: {
        container: null,
        video: {
          url: ""
        },
        subtitle: null,
        autoplay: false
      },
      player: null
    };
  },
  props: {
    url: String,
    path: String
  },
  async mounted() {
    this.options.container = document.getElementById("dplayer");
    let vttUrl = this.path + ".vtt";
    let url = this.url ? this.url : this.common.p_h0 + this.path;
    await this.common.axios
      .get("./api/public/ls?path=" + encodeURIComponent(vttUrl))
      .then(response => {
        if (response.data) {
          this.options.subtitle = {
            url: response.data.downloadUrl,
            type: "webvtt",
            fontSize: "25px",
            bottom: "10%",
            color: "#b7daff"
          };
        }
      })
      .catch(() => {});

    this.player = new DPlayer(this.options);
    this.player.switchVideo({
      url
    });
  },
  destroyed() {
    this.player.destroy();
  },
  watch: {
    url(val) {
      this.player.switchVideo({
        url: val
      });
    }
  }
};
</script>

<style>
@media screen and (min-width: 992px) {
  .dplayer {
    margin: 0 auto;
    height: 80vh;
  }
}

@media screen and (max-width: 992px) {
  .dplayer {
    margin: 0 auto;
    height: 35vh;
  }
}
</style>