import Vue from 'vue'
import VueRouter from 'vue-router'
import Home from '../views/Filelister.vue'
import Login from '../views/Login.vue'

Vue.use(VueRouter)

const routes = [
  {
    path: '/about',
    name: 'About',
    component: () => import('../views/Dashboard.vue')
  },
  {
    path: '/dashboard',
    name: 'Dashboard',
    component: () => import('../views/Dashboard.vue')
  }, {
    path: '/files',
    name: 'Files',
    component: () => import('../views/Filesmanager.vue')
  }, {
    path: '/cache',
    name: 'Cache',
    component: () => import('../views/Cache.vue')
  }, {
    path: '/setting',
    name: 'Setting',
    component: () => import('../views/Setting.vue')
  }, {
    path: '/home*',
    name: 'Home',
    component: Home
  }, {
    path: '/login',
    name: 'Login',
    component: Login
  },
  {
    path: '/*',
    name: 'Home1',
    component: Home
  },
]

const router = new VueRouter({
  routes
})

export default router
